import { connect } from "react-redux";
import isAuthHoc from "../HOCS/isAuthHoc";
import { compose } from "redux";
import {sendNewMessage,} from "../../redux/dialogs-reducer";
import Dialogs from "./Dialogs";
let mapStateToProps = (state) => {
  return {
    chatInfo: state.dialogs.dialogs,
    messagesInfo: state.dialogs.messages}}
export default compose(
  connect(mapStateToProps, {sendNewMessage}), //Проктдаыва в себя результат вредыдущего (так будут делать все последующие)
  isAuthHoc //Возми нижний, закинь в этот
)(Dialogs)
