import React from "react";
import Chat from "./Chat/Chat";
import s from "./dialogs.module.css";
import Message from "./Message/Message";
import {SenderMsgContainer} from "./SenderMsg";
export default function Dialogs(props) {
  const onSubmit = (dataItems) =>{
    props.sendNewMessage(dataItems.messageText)
    }
  const chat = props.chatInfo.map((d) => <Chat name={d.name} key={d.id} id={d.id} />);
  const message = props.messagesInfo.map((m) => (
    <Message message={m.message} key={m.id} id={m.id} />
  ));
  return (
    <div className={s.dialogs}>
      <div className={s.chats}>{chat}</div>
      <div className={s.messages}>{message}</div>
      <SenderMsgContainer onSubmit = {onSubmit}/>
    </div>
  );
}
