import s from './CustomTextarea.module.css'
  const CustomForm = ({meta : {touched, error}, children}) => {
    const showError = touched && error
   return(
       <div className={showError ? s.errorstyle2 : s.normalstyle2}>
          {children}
          {showError ? <span className={s.error}>{error}</span> : ''}
       </div> 
   )}
  export const CustomInput = (props) =>{
  return <CustomForm meta ={props.meta}><input {...props.input} {...props}/></CustomForm>}
  export const CustomTextarea =(props)=>{
  return <CustomForm meta ={props.meta}><textarea {...props.input} {...props}/></CustomForm>}