import React from "react";
import { connect } from "react-redux";
import { Navigate } from "react-router-dom";
const mapStateToPropsForRedirect = (state) => ({isAuth: state.auth.isAuth})
const isAuthHoc = (Component) => {
 class authComponent extends React.Component{
     render(){
        if(this.props.isAuth === false) return <Navigate to='/login'/>
        return <Component {...this.props} />
     }
   }
   const authContainerConnect = connect(mapStateToPropsForRedirect)(authComponent)
   return authContainerConnect
}
export default isAuthHoc