import React from "react";
import { Field, reduxForm } from "redux-form";
import {required, maxLength50} from '../../utils/validators/validators'
import { CustomTextarea } from "../commons/CustomTextarea";
import s from "./SenderMsg.module.css";
const SenderMsg = (props)=>{
  return (
    <div className={s.sendMsgArea}>
      <Field name={"messageText"}
       component={CustomTextarea} validate={[required, maxLength50]} />
      <button className={s.button} onClick={props.handleSubmit}>Send</button>
    </div>
  );
}
export const SenderMsgContainer = reduxForm({form: 'messages'})(SenderMsg)
